.loading-plate {
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: rgb(0, 0, 0);
  transition: 2s;
  z-index: 99;
  display: flex;
  justify-content: center;
  align-items: center;
}

.loading-plate__circle {
  width: 500px;
  height: 500px;
  background-color: burlywood;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  animation: "change-color" 2s infinite alternate;
}

.loading-plate__text {
  font-size: 48px;
  font-family: "Century";
  color: grey;
}

.loading-plate__hidden {
  opacity: 0;
  pointer-events: none;
}

@keyframes change-color {
  from {
    background-color: rgb(243, 79, 51);
  }
  to {
    background-color: rgb(51, 28, 75);
  }
}
